import _ from 'lodash'
export default {
  data() {
    return {
      // 用户添加的商品数据
      addForm: {
        goods_name: '',
        goods_cat: '',
        goods_price: 0,
        goods_number: 0,
        goods_weight: 0,
        goods_introduce: '',
        pics: [],
        attrs: []
      },
      // 获取的分类数据
      cateList: [],
      // 选择的分类
      cateKeys: [],
      // 获取到的动态参数
      dtParamsList: [],
      // 获取到的静态属性
      jtParamsList: [],
      // 上传图片
      fileList: [],
      // 图片上传地址
      uploadUrl: 'http://127.0.0.1:8888/api/private/v1/upload',
      // 图片上传头部
      uploadHeader: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 图片大图地址
      previewPath: '',
      // 步骤条和标签页显示
      activeNum: '0',
      // 对话框可视度
      dialogVisible: false,
      // 配置级联选择器
      props: {
        label: 'cat_name',
        value: 'cat_id',
        children: 'children',
        expandTrigger: 'hover'
      },
      // 验证规则
      addRules: {
        goods_name: [
          { required: true, message: '请输入名称', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '请输入价格', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '请输入重量', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '请输入数量', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // 获取分类数据
    async getCateList() {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.console.error('获取数据失败')
      }
      this.cateList = res.data
    },
    // 监听级联选择器变化
    handleChange() {
      console.log(this.cateKeys)
    },
    // 标签页切换
    beforeTagLeave(activeName, oldActiveName) {
      if (oldActiveName === '0' && this.cateKeys.length !== 3) {
        this.$message.error('请选择商品分类')
        return false
      }
    },
    // 根据pane面板渲染所匹配的数据
    async tabClick(activeIndex) {
      console.log(this.attrId)
      if (activeIndex.index === '1') {
        const { data: res } = await this.$http.get(
          `categories/${this.attrId}/attributes`,
          { params: { sel: 'many' } }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('获取数据失败')
        }
        res.data.forEach((item) => {
          item.attr_vals =
            item.attr_vals.length !== 0 ? item.attr_vals.split(' ') : []
        })
        this.dtParamsList = res.data
      } else if (activeIndex.index === '2') {
        const { data: res } = await this.$http.get(
          `categories/${this.attrId}/attributes`,
          { params: { sel: 'only' } }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('获取数据失败')
        }
        this.jtParamsList = res.data
        console.log(this.jtParamsList)
      }
    },
    // 监听图片上传成功事件
    handelSuccess(response) {
      // 1.将临时路径保存到pic对象
      const picInfo = { pic: response.data.tmp_path }
      // 2.将pic保存到pics数组中
      this.addForm.pics.push(picInfo)
      console.log(this.addForm)
    },
    // 查看照片详情
    handlePreview(file) {
      this.previewPath = file.response.data.url
      this.dialogVisible = true
    },
    // 删除照片
    handleRemove(file) {
      // 获取该图片路径
      const filePath = file.response.data.tmp_path
      // 找到该图片索引
      const i = this.addForm.pics.findIndex((x) => x.pic === filePath)
      // 根据索引删除该图片
      this.addForm.pics.splice(i, 1)
      console.log(file)
    },
    // 添加商品
    async confirmAddForm() {
      this.$refs.addRef.validate((valid) => {
        if (!valid) {
          return this.$message.error('请完善信息')
        }
      })
      // 通过预验证 执行业务逻辑
      // 分类
      const form = _.cloneDeep(this.addForm)
      form.goods_cat = this.cateKeys.join(',')
      console.log(form)
      // 动态参数
      this.dtParamsList.forEach((item) => {
        const newInfo = {
          attr_id: item.attr_id,
          attr_value: item.attr_vals.join(' ')
        }
        this.addForm.attrs.push(newInfo)
      })
      // 静态参数
      this.jtParamsList.forEach((item) => {
        const newInfo = { attr_id: item.attr_id, attr_value: item.attr_vals }
        this.addForm.attrs.push(newInfo)
      })
      form.attrs = this.addForm.attrs
      const { data: res } = await this.$http.post('goods', form)
      if (res.meta.status !== 201) {
        console.log(res)
        return this.$message.error('添加失败')
      }
      this.$message.success('添加成功')
      this.$router.push('/goods')
    }
  },
  computed: {
    attrId() {
      if (this.cateKeys.length === 3) {
        return this.cateKeys[2]
      }
      return null
    }
  }
}
